import { Component, useState, React } from 'react';
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import { Fade, Box } from '@mui/material';
import Button from '@mui/material/Button';
import SendIcon from '@mui/icons-material/Send';
import IconButton from '@mui/material/IconButton';
import backgroundImage from "./img/tftdle-chooseyourchampion.png";
import sendIcon from "./img/tftdle-send.png";


const styles = {
    '& .MuiAutocompleteInputRoot': {
        backgroundImage: `url(${backgroundImage})`,
        backgroundSize: 'cover',
        backgroundRepeat: 'no-repeat',
    }
  };

const MyButton = ({handleSubmit}) => {
    const handleKeyDown = (event) => {
        if (event.key === 'Enter') {
            handleSubmit()
        }
    }

    return <IconButton onClick={handleSubmit}
        variant="contained"
        style={{ marginTop: "25px", }} 
        onKeyDown={handleKeyDown}>
        <SendIcon />
    </IconButton>
}

class ComboBox extends Component {

    constructor(props) {
        super(props);
        this.state = {
            selectedOptions: []
        };
        this.handleSubmit = this.handleSubmit.bind(this)
        this.handleChange = this.handleChange.bind(this)
    }

    handleChange = (event, value) => {
        this.setState({ selectedOptions: value });
    };

    handleSubmit = () => { this.props.handleSubmit(this.state.selectedOptions) }

    onKeyPressEnter = () => {
        // console.log("Enter pressed")
    }

    render() {
        // console.log(this.props.champions);

        var champions = this.props.champions;
        return (
            <div className="module-border-wrap" style={{
                margin: "20px",
                height: "100px",
                // border: "2px solid",
            }}>
                <Autocomplete
                    className="customAutocomplete"
                    freeSolo={true}
                    isOptionEqualToValue={(option, value) => option.id == value.id}
                    sx={{
                        width: 300, margin: "auto", display: "inline-block", padding: "10px",
                        // borderImage: "linear-gradient(to right, red, orange)",
                        // border: "2px solid blue",
                        "& .MuiOutlinedInput-root": {
                            border: "none",
                            // borderImage: "linear-gradient(to right, red, orange)",
                            // borderRadius: "4px",
                            // background: "linear-gradient(to right, rgb(255, 138, 0, 0.6), rgb(229, 46, 113,0.6)) rgba(0,0,0,0.1)",
                            // backgroundColor:"rgba(0,0,0,0.1)"

                            // borderImage: `url(${backgroundImage})`,
                            // borderRadius: "4px",
                            // background: `url(${backgroundImage})`,
                            // backgroundColor:"rgba(0,0,0,0.1)"
                            //     padding: "0"
                        },
                        "& .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
                            border: "none",
                        //     borderImage: "linear-gradient(to right, red, orange)",
                        },
                        // backgroundImage: `url(${backgroundImage})`,

                        backgroundImage: `url(${backgroundImage})`,
                        backgroundPosition: 'center center',
                        backgroundSize: 'contain',
                        backgroundRepeat: 'no-repeat',                        
                    }}
                    options={champions}
                    autoHighlight
                    getOptionLabel={(option) => option.name}
                    onChange={this.handleChange}
                    renderOption={(props, option) => (
                        <Box component="li" sx={{ '& > img': { mr: 2, flexShrink: 0 } }} {...props}>
                            <img
                                loading="lazy"
                                width="40"
                                height="40"
                                src={option.imgUrl}
                                srcSet={option.imgUrl}
                                alt=""
                            />
                            {option.name}
                        </Box>
                    )}
                    renderInput={(params) => (
                        <TextField
                            {...params}
                            // hiddenLabel
                            // id="filled-hidden-label-normal"
                            // variant="filled"
                            placeholder="Choose a champion..."
                            inputProps={{
                                ...params.inputProps,
                                // autoComplete: 'new-password', // disable autocomplete and autofill
                                onKeyDown: (e) => {
                                    if (e.key === 'Enter') {
                                     this.handleSubmit()
                                    }
                              },
                            }}
                        />
                    )}
                />
                <Button onClick={this.handleSubmit}
                    style={{
                    // border: " 1px solid",
                    height: "60px",                        
                    backgroundImage: `url(${sendIcon})`,
                    backgroundPosition: 'center center',
                    backgroundSize: 'cover',
                    backgroundRepeat: 'no-repeat', 
                    marginTop: "30px", display: "inline-block", padding: "10px", }}>
                    {/* <SendIcon/> */}
                </Button>
            </div>
        );

    }
}

export default ComboBox