import React, { Component, useState, useEffect } from 'react';
import { Button, ButtonGroup, Container, Table } from 'reactstrap';
import AppNavbar from './AppNavbar';
import './ChampionList.css';
import { Fade, Box, TableCell, tableCellClasses } from '@mui/material';
import ComboBox from './ComboBox';
import styled from '@emotion/styled'
import ToolTipTableCell from './ToolTipTableCell';
import Confetti from 'react-confetti'
import { isValidDateValue } from '@testing-library/user-event/dist/utils';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import IconButton from '@mui/material/IconButton';
import secureLocalStorage from "react-secure-storage";
import tableBackgroundImage from "./img/tftdlebox4k.png";
import backgroundImage from "./img/tftdle-chooseyourchampion.png";
import headersImage from "./img/tftdle-tableTitles.png";
import { height } from '@mui/system';
import zIndex from '@mui/material/styles/zIndex';
import { ColorRing } from 'react-loader-spinner'



function MyConfetti({ isAnswered, handler }) {
    const [show, setShow] = useState(false)
    const [timedOut, setTimedOut] = useState(false)
    // On componentDidMount set the timer
    useEffect(() => {
        const timeId = setTimeout(() => {
            // After 3 seconds set the show value to false
            setShow(true)
        }, 6000)

        return () => {
            clearTimeout(timeId)
        }
    }, []);
    // console.log("SHOW: " + show)
    if (show && isAnswered) {
        if (!timedOut) {
            setTimedOut(true)
        }
        return <Confetti tweenDuration={5000}></Confetti>
    }
    else
        return;
}

function MyEmote({ isAnswered }) {
    const [show, setShow] = useState(false)

    // On componentDidMount set the timer
    useEffect(() => {
        const timeId = setTimeout(() => {
            // After 3 seconds set the show value to false
            setShow(true)
        }, 6000)

        return () => {
            clearTimeout(timeId)
        }
    }, []);
    // console.log("SHOW: " + show)
    if (show && isAnswered) {
        return <Fade in={true}
            timeout={{ enter: 1000, exit: 250 }}
            style={{ transitionDelay: `100ms` }}>
            <div style={{
                margin: "20px",
                height: "100px",
            }}>
                <img src='https://raw.communitydragon.org/latest/game/assets/loadouts/summoneremotes/flairs/penguin_happy_greetings_selector.png'></img></div></Fade>
    }

    else
        return;
}

function MyComboBox({ isAnswered, champions, handleSubmit }) {
    if (!isAnswered) {
        return <ComboBox className="pad" champions={champions} handleSubmit={handleSubmit}></ComboBox>
    }
    return <ComboBox className="pad" champions={champions} handleSubmit={handleSubmit} style={{ display: "none" }}></ComboBox>;
}

function MySolved({ numberOfPeopleSolved, hideComboBox, date }) {
    if (!hideComboBox)
        fetch('/solved')
            .then(response => response.json())
            .then(data => { numberOfPeopleSolved = data })
    return <div>
        <span style={{ color: "yellow" }}>{numberOfPeopleSolved} </span> people solved todays puzzle! <span style={{ color: "yellow" }}> #{date} </span>
    </div>
}

function MyShare({ coloursGuessed, championsGuessed, getGuessColour, answer, date }) {
    const green = "#28aa0a"
    const red = "#D4312B"
    const orange = "#f07e0f"

    const guessColours = championsGuessed.map(champ => getGuessColour(champ, answer))
    var i = 0
    const guessPattern = guessColours.map(guessColour => {
        i++
        return <div key={i}>
            {guessColour[0] == green ? " " : " "}
            {guessColour[1] == green ? " " : " "}
            {guessColour[2] == green ? " " : guessColour[2] == orange ? "" : " "}
            {guessColour[3] == green ? " " : guessColour[3] == orange ? "" : " "}
            {guessColour[4] == green ? " " : " "}
        </div>
    })
    var text = "";
    const textToCopy = guessColours.map(guessColour => {
        var text = ""
        text += (guessColour[0] == green ? "  " : "  ") + (guessColour[1] == green ? "  " : "  ") + (guessColour[2] == green ? "  " : guessColour[2] == orange ? " " : "  ") + (guessColour[3] == green ? "  " : guessColour[3] == orange ? " " : "  ") + (guessColour[4] == green ? " \n" : " \n")
        return text
    }).map(t => text += t)
    return <div style={{ width: "650px", margin: "auto", backgroundImage: "linear-gradient(to right, rgb(252, 204, 78, 0.8), rgb(249, 148, 65, 0.8))", border: "2px solid white" }}>
        <p style={{
            fontSize: "20px",
            fontFamily: "Roboto",
            color: "white",
            width: "100%",
            textShadow: "0 0 3px black"
        }}> I have guessed todays Tftdle in {championsGuessed.length} tries.</p>
        {guessPattern}
        <IconButton onClick={() => { navigator.clipboard.writeText('I have guessed todays Tftdle #' + date + ' in ' + championsGuessed.length + ' tries.\n' + text + 'Try it on: https://tftdle.net') }}
            variant="contained"
            style={{ marginTop: "auto", }} >
            <ContentCopyIcon />
        </IconButton>
    </div>

}
class ChampionList extends Component {

    constructor(props) {
        super(props);
        this.state = {
            champions: [],
            championsGuessed: [],
            answer: {},
            answerResult: false,
            showConfetti: false,
            hideComboBox: true,
            toggleInfoModal: false,
            numberOfPeopleSolved: 0,
            coloursGuessed: [],
            oldAnswer: {},
            date: 0,
            loading: true,
        };
    }

    setWithExpiry(key, value, interval) {
        const now = new Date();
        const item = {
            value: value,
            expiry: now.getTime() + interval,
        };
        // console.log(item.expiry)
        // console.log()
        secureLocalStorage.setItem(key, JSON.stringify(item));
    };

    getWithExpiry(key) {
        const itemStr = secureLocalStorage.getItem(key)
        if (!itemStr) {
            return null;
        }
        const item = JSON.parse(itemStr);
        const now = new Date();
        if (now.getTime() > item.expiry) {
            secureLocalStorage.removeItem(key);
            return null;
        }
        return item.value;
    }

    setStateWithLocalStorage(key, state) {
        // console.log("HERE")
        // console.log(state)
        // if(key == "state" || key == 'answer')
        this.setWithExpiry(key, state, this.timeUntilMidnight());
        super.setState(state);
    }
    // componentDidUpdate() {
    //     window.localStorage.setItem('state', JSON.stringify(this.state));
    // }

    componentDidUpdate(prevProps, prevState) {
        // this.setWithExpiry('state', this.state, 1000000)
        this.setWithExpiry('championsGuessed', this.state.championsGuessed, this.timeUntilMidnight())
        if (!this.isEmpty(this.state.answer) && !this.isEmpty(this.state.oldAnswer) && this.state.answer.name !== this.state.oldAnswer.name) {
            secureLocalStorage.clear()
            window.location.reload();
        }
    }

    componentDidMount() {


        this.setState({ loading: true })
        setTimeout(() => {
            this.setState({ loading: false });
        }, 500);
        fetch('/all')
            .then(response => response.json())
            .then(data => this.setState({ champions: data }));

        fetch('/winner')
            .then(response => response.json())
            .then(data => this.setState({ answer: data }))

        fetch('/solved')
            .then(response => response.json())
            .then(data => this.setState({ numberOfPeopleSolved: data }))

        fetch('/date')
            .then(response => response.json())
            .then(data => this.setState({ date: data }))

        try {
            this.getStateFromLocalStorage('answerResult', 'answerResult')
            this.getStateFromLocalStorage('champions', 'champions')
            this.getStateFromLocalStorage('championsGuessed', 'championsGuessed')
            this.getStateFromLocalStorage('answer', 'answer')
            this.getStateFromLocalStorage('hideComboBox', 'hideComboBox')
        } catch (e) { }

    }

    getStateFromLocalStorage = (key, stateName) => {
        const state = this.getWithExpiry(key);
        // console.log(state);
        if (state != null) {
            // console.log(state);
            // console.log(this.state.answer)
            this.setState({ [stateName]: state });
            if (stateName == 'answer') {
                this.setState({ oldAnswer: state })
            }
        }

    }

    isEmpty(obj) {
        for (const prop in obj) {
            if (Object.hasOwn(obj, prop)) {
                return false;
            }
        }

        return true;
    }

    isValidChoice = (selectedOptions) => {
        // console.log(selectedOptions)
        if (selectedOptions == null)
            return false;
        const { championsGuessed } = this.state
        if (Object.keys(selectedOptions).length > 0) {
            if (championsGuessed.length == 0) {
                return true;
            }
            for (var champ of championsGuessed) {
                // console.log(champ)
                if (champ.name == selectedOptions.name)
                    return false
            }
            return true;
        }
        return false;
    }
    handleSubmit = (selectedOptions) => {

        // console.log("HERE")
        // console.log(selectedOptions);
        // console.log(selectedOptions.length);

        if (this.isValidChoice(selectedOptions) && Object.keys(selectedOptions).length > 0) {
            // console.log("CHANGE")
            var array1 = [...this.state.championsGuessed];
            array1.unshift(selectedOptions)
            this.setState(prevState => ({
                championsGuessed: [selectedOptions, ...prevState.championsGuessed]
            }))


            if (this.isCorrectAnswer(selectedOptions)) {
                this.setState({ answerResult: this.state.answerResult = true })
            }
            var array = [...this.state.champions]; // make a separate copy of the array
            var index = array.indexOf(selectedOptions)
            if (index !== -1) {
                array.splice(index, 1);
                this.setState({ champions: array });
                this.setWithExpiry('champions', array, this.timeUntilMidnight())
            }

            // this.setState(prevState => ({
            //     coloursGuessed: [...prevState.coloursGuessed, this.getGuessColour(selectedOptions)]
            // }))

        }
    }

    toggleInfoModal = () => {
        this.setState({ toggleInfoModal: !this.toggleInfoModal })
    }

    textBoxHandle = () => {
        this.setState({
            hideComboBox: false
        })
        // console.log("HIDECOMBOBOX: " + this.hideComboBox)
        this.setWithExpiry("hideComboBox", false, this.timeUntilMidnight())
        // localStorage.setItem("hideComboBox", JSON.stringify(false))
    }

    timeUntilMidnight() {
        var midnight = new Date();
        midnight.setUTCHours(24, 0, 0, 0);
        // midnight.setMinutes(0);
        // midnight.setSeconds(0);
        // midnight.setMilliseconds(0);
        // console.log("T1: " + Date.parse(new Date))
        // console.log("T2: " + new Date().getTime())
        return (midnight.getTime() - new Date().getTime());
    }

    isCorrectAnswer = (guess) => {
        this.setWithExpiry('answer', this.state.answer, this.timeUntilMidnight())
        var count = 0;
        if (this.state.answer.gender == guess.gender) {
            count++;
        }
        if (this.state.answer.cost == guess.cost) {

            count++;
        }
        if (this.state.answer.originTrait == guess.originTrait) {

            count++;
        }
        if (this.state.answer.classTrait == guess.classTrait) {
            count++;
        }
        if (this.state.answer.rangeType == guess.rangeType) {
            count++;
        }

        // console.log(new Date().toUTCString())

        // console.log("COUNT: " + count);
        if (count == 5) {
            const timeId = setTimeout(() => {
                this.textBoxHandle()
            }, 6000)
            this.setWithExpiry('answerResult', true, this.timeUntilMidnight())
            // console.log("result: " + this.answerResult)
            if (this.answerResult != true) {
                fetch('/solve')
                    .then(response => response.json())
            }
            return true;
        }
        return false;
    }
    getGuessColour = (guess, answer) => {
        const green = "#28aa0a"
        const red = "#D4312B"
        const orange = "#f07e0f"
        // console.log(guess);
        // console.log(answer);
        var result = [];
        if (answer.gender == guess.gender) {
            result.push(green)
        }
        else
            result.push(red);
        if (answer.cost == guess.cost) {
            result.push(green)
        }
        else
            result.push(red);

        if (answer.originTrait == guess.originTrait) {
            result.push(green)

        }
        else {
            var colour = red;
            var ans = answer.originTrait.replace(/ /g, '');
            var ansArr = ans.split(",")

            var guess1 = guess.originTrait.replace(/ /g, '');
            var guessArr = guess1.split(",")

            for (var word1 of ansArr) {
                for (var word2 of guessArr) {
                    if (word1 == word2) {
                        colour = orange
                    }
                }
            }

            result.push(colour);
        }
        if (answer.classTrait == guess.classTrait) {
            result.push(green)
        }
        else {
            var colour = red;
            var ans = answer.classTrait.replace(/ /g, '');
            var ansArr = ans.split(",")

            var guess1 = guess.classTrait.replace(/ /g, '');
            var guessArr = guess1.split(",")

            for (var word1 of ansArr) {
                for (var word2 of guessArr) {
                    if (word1 == word2) {
                        colour = orange
                    }
                }
            }

            result.push(colour);
        }
        if (answer.rangeType == guess.rangeType) {
            result.push(green)
        }
        else
            result.push(red);

        return result;
    }
    render() {
        const { champions, championsGuessed, answer, answerResult, showConfetti, hideComboBox, numberOfPeopleSolved, coloursGuessed, date, loading } = this.state;

        // console.log(JSON.parse(window.localStorage.getItem('state')))
        // console.log(answer);
        // console.log(championsGuessed)
        // console.log(champions);
        // console.log(hideComboBox);
        // console.log("RESULT: " + answerResult)
        // console.log("CREATING TABLE")

        const championList = championsGuessed.map(champion => {
            // console.log("PRINTING" + champion.name)
            const guessColours = this.getGuessColour(champion, answer);
            return <tr key={champion.name}>
                <Fade in={true}
                    timeout={{ enter: 1000, exit: 250 }}
                    style={{ transitionDelay: `100ms` }}>

                    <td style={{ border: "1px solid rgb(255, 255, 255, 0)", backgroundColor: "rgb(0, 0, 0, 0)", margin: "0 px", padding: "0px" }} >
                        <a title={champion.name}><img src={champion.imgUrl} alt="some" height="80px"></img></a>
                    </td>
                </Fade>
                <Fade in={true}
                    timeout={{ enter: 1000, exit: 250 }}
                    style={{ transitionDelay: `1100ms` }}>
                    <td style={{ border: "1px solid rgb(255, 255, 255)", boxShadow: "inset 0 0 6px black", backgroundColor: guessColours[0], margin: "0 px", padding: "0px" }}>{champion.gender}</td>
                </Fade>
                <Fade in={true}
                    timeout={{ enter: 1000, exit: 250 }}
                    style={{ transitionDelay: `2100ms` }}>
                    <td style={{ border: "1px solid rgb(255, 255, 255)", boxShadow: "inset 0 0 6px black", backgroundColor: guessColours[1], margin: "0 px", padding: "0px" }}>{champion.cost}</td>
                </Fade>
                <Fade in={true}
                    timeout={{ enter: 1000, exit: 250 }}
                    style={{ transitionDelay: `3100ms` }}>
                    <td style={{ border: "1px solid rgb(255, 255, 255)", boxShadow: "inset 0 0 6px black", backgroundColor: guessColours[2], margin: "0 px", padding: "0px" }}>{champion.originTrait}</td>
                </Fade>
                <Fade in={true}
                    timeout={{ enter: 1000, exit: 250 }}
                    style={{ transitionDelay: `4100ms` }}>
                    <td style={{ border: "1px solid rgb(255, 255, 255)", boxShadow: "inset 0 0 6px black", backgroundColor: guessColours[3], margin: "0 px", padding: "0px" }}>{champion.classTrait}</td>
                </Fade>
                <Fade in={true}
                    timeout={{ enter: 1000, exit: 250 }}
                    style={{ transitionDelay: `5100ms` }}>
                    <td style={{ border: "1px solid rgb(255, 255, 255)", boxShadow: "inset 0 0 6px black", backgroundColor: guessColours[4], margin: "0 px", padding: "0px" }}>{champion.rangeType}</td>
                </Fade>
            </tr>
        });


        // console.log(answerResult)
        return (
            <div>
                <AppNavbar />
                <Container fluid>
                    {loading ? (
                        <div style={{ width: "650px", margin: "auto" }} >
                            <ColorRing
                                visible={true}
                                height="80"
                                width="80"
                                ariaLabel="blocks-loading"
                                wrapperStyle={{}}
                                wrapperClass="blocks-wrapper"
                                colors={['#e15b64', '#f47e60', '#f8b26a', '#fccc4e', '#f99441']}
                            />
                        </div>
                    ) : (
                        <div>
                            <div className={(answerResult ? 'show' : 'hidden')}>
                                {answerResult && <MyConfetti isAnswered={answerResult} handler={this.textBoxHandle} />}
                            </div>
                            <div style={{
                                margin: "20px",
                                height: "100px",
                            }}>
                                {hideComboBox && <MyComboBox isAnswered={answerResult} champions={champions} handleSubmit={this.handleSubmit} />}
                                {answerResult && <MyEmote isAnswered={answerResult} />}
                            </div>
                            <div className='pad' style={{
                                width: "650px", margin: "auto", backgroundImage: "linear-gradient(to right, rgb(249, 148, 65, 0.8), rgb(252, 204, 78, 0.8))", border: "2px solid white", position: "relative"


                            }}>
                                <img src={headersImage} style={{
                                    width: "642px",
                                    height: "50px",
                                    marginLeft: "-320px",
                                    padding: "0px",
                                    position: "fixed",
                                    zIndex: "1"
                                }}></img>
                                <Table className="align-middle"
                                    borderless
                                    // hover
                                    responsive
                                    style={{
                                        position: "relative",
                                        borderCollapse: "separate",
                                        borderSpacing: "5px 5px",
                                        fontFamily: "Roboto",
                                        color: "white",
                                        width: "100%",
                                        textShadow: "0 0 3px black",
                                        zIndex: "4"
                                        // backgroundImage: `url(${tableBackgroundImage})`,
                                        // backgroundPosition: 'center center',
                                        // backgroundSize: 'cover',
                                        // backgroundRepeat: 'no-repeat', 
                                    }}
                                >
                                    <thead>
                                        <tr style={{ zIndex: "4" }}>
                                            <th width="80px">Champion</th>
                                            <th width="80px">Gender</th>
                                            <th width="80px">Cost</th>
                                            <th width="80px">Origin</th>
                                            <th width="80px">Class</th>
                                            <th width="80px">Range Type</th>
                                        </tr>
                                    </thead>
                                    <tbody >
                                        {championList}
                                    </tbody>
                                </Table>
                            </div>

                            <div
                                style={{
                                    fontSize: "20px",
                                    fontFamily: "Roboto",
                                    color: "white",
                                    width: "100%",
                                    textShadow: "0 0 3px black"
                                }}>
                                <MySolved numberOfPeopleSolved={numberOfPeopleSolved} hideComboBox={hideComboBox} date={date}></MySolved>
                            </div>
                            <div>
                                {!hideComboBox && <MyShare coloursGuessed={coloursGuessed} championsGuessed={championsGuessed} getGuessColour={this.getGuessColour} answer={answer} date={date}></MyShare>}
                            </div>
                        </div>
                    )}
                </Container>
            </div>
        );
    }
}


export default ChampionList;